.input_wrp {
    position: relative;
}

.input {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 16px 20px;
    width: 100%;
    background: #f9faf9;
    border: 1px solid #d5dddc;
    border-radius: 8px;
    flex: none;
    order: 1;
    flex-grow: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 140.02%;
    color: #303535;

    &_icon {
        width: 25px;
        position: absolute;
        top: 15px;
        right: 18px;
        cursor: pointer;
    }
}
